	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"We're Back","date":"19 Apr 2003","slug":"/2003-04-19-were-back/","tags":"Y2003"},"html":"<p>We're back in Old Europe, after flying all night. We won't be coherent, though, until probably Monday. The Lufthansa flight was actually pretty good, mostly because the plane was rather empty and the 3 of us had 5 seats. </p>\n<p>Did anything happen while we were gone?</p>"},"next":{"fields":{"title":"Just A Note To Say...","date":"15 Apr 2003","slug":"/2003-04-15-just-a-note-to-say/"}},"previous":{"fields":{"title":"Backpedal in Place","date":"20 Apr 2003","slug":"/2003-04-20-backpedal-in-place/"}}},{"node":{"fields":{"title":"Just A Note To Say...","date":"15 Apr 2003","slug":"/2003-04-15-just-a-note-to-say/","tags":"Y2003"},"html":"<p>(Anyone familiar with current children's videos will know from the headline what movie Christopher has been watching constantly the past two weeks.)</p>\n<p>We're still in Florida, but have been following events in Iraq via the St. Petersburg Times and occasional fleeting glimpses at TV. When the newspaper headline is all upper case, we can tell that it was a big day.</p>\n<p>The TV critic for the Times, Eric Deggens, had a nice observation about Fox News today: \"Roger Ailes... has excelled by capitalizing on an essential truth about American TV viewers: Often we say we want one thing, when we really want another. We may say we want objective coverage, but we really want news that fits our own cultural world view.\" I don't think that applies only to American viewers...</p>\n<p>I'll have to see if I can pick up that Kagan book that <a href=\"http://ahawkins.org/comments.php?id=P1064_0_1_0\">Alwin Hawkins recommended</a> before we leave. The Times had a good review of the book and an Op-Ed by him as well (Kagan, not Hawkins).</p>"},"next":{"fields":{"title":"Rockaway Beach","date":"04 Apr 2003","slug":"/2003-04-04-rockaway-beach/"}},"previous":{"fields":{"title":"We're Back","date":"19 Apr 2003","slug":"/2003-04-19-were-back/"}}},{"node":{"fields":{"title":"Rockaway Beach","date":"04 Apr 2003","slug":"/2003-04-04-rockaway-beach/","tags":"Y2003"},"html":"<p>\"It's not hard, not far to reach<br />\nWe can hitch a ride to Rockaway Beach\"</p>\n<p>Actually, we are at Indian Rocks Beach, just south of Clearwater, Florida. Luckily they don't \"blast out the disco on the radio\" like in the Ramones song. Sunny, quiet, and connected, thanks to a $10 one-month account at <a href=\"http://www.budgetdíalup.com\">Budget Dialup</a>, which is working more reliably than the big-time provider Mama uses to dial into her employer's VPN.</p>\n<p>I was going to get a pre-paid card for my spare cellphone (Motorola Timeport, cost &euro;13 used)  for the two weeks, but US cellular service is so expensive and confusing, I think I'll just pay the roaming charges for my German provider for the few times I'm going to call. </p>\n<p>By the way, the WiFi on the Lufthansa flight was out of service, although they offered plain-old-ethernet. Mama used up her battery reading her mail at work, and they only offered electrical sockets in business and first class. Sitting in economy with a three-year-old is not a optimal environment for using a notebook anyway. Despite the fears of World Wide Klein, Lufthansa did not lose any of our luggage, but we only had two suitcases.</p>"},"next":{"fields":{"title":"Master Builder","date":"02 Apr 2003","slug":"/2003-04-02-master-builder/"}},"previous":{"fields":{"title":"Just A Note To Say...","date":"15 Apr 2003","slug":"/2003-04-15-just-a-note-to-say/"}}}],"pathPrefix":"","first":false,"last":false,"index":772,"pageCount":1101}}

